import React, {Fragment, useState} from 'react';

function InputField({state, setState}) {
	return <input type="text" placeholder="ใส่ข้อความที่นี่" value={state.txt} onChange={(e) => {
		setState({
			...state,
			txt: e.target.value
		});
	}}/>;
}

function OutputExplosion({state, setState}){
	function explode(str){
		const txtmatch = /[\u0E01-\u0E30]|\u0E32|[\u0E3F-\u0E46]|[\u0E50-\u0E5B]/g;
		return (str.split(txtmatch) || ['']).join(' ');
	}
	return <p>
		{explode(state.txt)}
	</p>
}

function Index() {
	const [state, setState] = useState({txt: ''});
	return (
	  <Fragment>
		<h1>Exploder</h1>
		<InputField state={state} setState={setState} />
		<OutputExplosion state={state} setState={setState} />
	  </Fragment>
	);
  }

export default Index;